.endpoint-case-hidden-anchor {
	position: relative;
	top: -20px;
}

.endpoint-case-host {
	cursor: pointer;
	border-bottom: 1px dotted #333;;
}

.endpoint-case-copy-to-clipboard {
	position: absolute;
	right: 20px;
	cursor: pointer;
}

.endpoint-case-descr {
	margin-bottom: 45px;
}
	.endpoint-case-descr code {
		padding: .2em .4em;
		margin: 0;
		font-size: 85%;
		background-color: rgba(27,31,35,.05);
		border-radius: 3px;
	}

.request-section {
	background: #fff;
	margin: -24px;
	padding: 24px;
	position: relative;
}

.request-section + .request-section {
	margin-top: 20px;
}

.request-section-label {
	background: rgba(0,0,0,.1);
    position: absolute;
    left: 0;
    top: 0;
    padding: 0 5px;
    border-radius: 0 0 4px 0;
}

.request-section-extra {
	position: absolute;
	top: 0;
	right: 0;
	white-space: nowrap;
}

.request-section-body {
	margin-top: 5px;
}

.access-selector {
	margin-left: 10px;
}

.access-selector-tab {
	cursor: pointer;
	background: rgba(175, 236, 255, .5);
	margin-left: 1px;
	display: inline-block;
	padding: 0 5px;
	border-radius: 0 0 4px 4px;
	transition: all .2s;
	vertical-align: top;
}

.access-selector-tab:hover {
	background: rgba(175, 236, 255, .8);
}

.access-selector-tab-active,
.access-selector-tab-active:hover {
	font-size: 110%;
	background: rgb(138, 228, 255);
	box-shadow: 0 1px 1px rgba(0,0,0,.1);
}

.access-selector-badge {
	padding-top: 10px;
}

.ant-tooltip-inner p {
	margin: 0;
}

.request-form-item.required-true::before {
	content: '*';
	display: block;
	position: absolute;
	margin-left: -10px;
	color: #ff4d4f;
}

.request-form-item + .request-form-item {
	margin-top: 5px;
}

.request-section .hljs {
	background-color: transparent;
}

.endpoint-param-value {
	display: inline-block;
	max-width: 50vh;
	overflow: hidden;
	text-overflow: ellipsis;
}
